import React, { useEffect, useState } from 'react';
import { Button, Col, Container, Row, Table } from 'reactstrap';
import { Rep } from '../Repetition/Rep';

export const Serie = ({ title, data, columns }) => {
  const [_series, setSeries] = useState(series);

  useEffect(() => {}, []);

  const handleOnSave = () => {
    alert('saving');
  };

  const handleOnAdd = () => {
    setdata([..._data, { id: _series.length + 1, num: 0, weigth: 0, editable: true }]);
  };

  const handleOnDelete = () => {
    setSeries(_series.filter(item => item.id !== _series.length));
  };

  const onItemChange = item => {
    setSeries(
      _series.map(serie => {
        if (serie.id === item.id) {
          return item;
        }
        return serie;
      }),
    );
  };

  return (
    <Container>
      <Row>
        <h2>{title}</h2>
      </Row>
      <Row>
        <Table>
          <thead>
            <tr>
              {_columns.map(c => (
                <th>{c}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {_series.map(item => (
              <Rep key={item.id} item={item} onItemChange={onItemChange}></Rep>
            ))}
          </tbody>
        </Table>
      </Row>
      <Row>
        <Col xs={8}>
          <Button color="success" onClick={handleOnSave}>
            Save
          </Button>
        </Col>
        <Col xs={4}>
          <Button color="danger" onClick={handleOnDelete}>
            -
          </Button>
          <Button color="primary" onClick={handleOnAdd}>
            +
          </Button>
        </Col>
      </Row>
    </Container>
  );
};
